'use client';

import Link from 'next/link';
import { FaBars, FaTimes } from 'react-icons/fa';
import { useState, useEffect } from 'react';
import { usePathname } from 'next/navigation';

const Navbar = () => {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const pathname = usePathname();

  // Check if the current path matches the link
  const isActive = (path: string) => {
    return pathname === path;
  };

  const toggleMobileMenu = () => {
    setIsMobileMenuOpen(!isMobileMenuOpen);
  };

  // Close mobile menu when clicking a link
  const closeMobileMenu = () => {
    setIsMobileMenuOpen(false);
  };

  // Close mobile menu when window is resized beyond mobile breakpoint
  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth >= 768) {
        setIsMobileMenuOpen(false);
      }
    };
    
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const navigationItems = [
    { href: '/', label: 'Home' },
    { href: '/about', label: 'JIVO ENERGY' },
    { href: '/projects', label: 'Projects' },
    { href: '/esg', label: 'ESG' },
    { href: '/csr', label: 'CSR' },
    { href: '/media', label: 'Media' },
    { href: '/gallery', label: 'Gallery' },
    { href: '/careers', label: 'Careers' }
  ];

  // Split navigation items into two arrays
  const leftNavItems = navigationItems.slice(0, 4);
  const rightNavItems = navigationItems.slice(4);

  return (
    <nav className="fixed top-0 w-full z-40 transition-all duration-300">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center justify-end h-20">
          {/* Mobile menu button */}
          <button 
            className="md:hidden relative z-50 p-2 rounded-lg hover:bg-[#4ADE80]/10 transition-colors duration-200"
            onClick={toggleMobileMenu}
            aria-label="Toggle mobile menu"
          >
            <div className={`transition-all duration-300 ${isMobileMenuOpen ? 'opacity-100' : 'opacity-0'} absolute inset-0 flex items-center justify-center`}>
              <FaTimes size={24} className="text-[#4ADE80]" />
            </div>
            <div className={`transition-all duration-300 ${isMobileMenuOpen ? 'opacity-0' : 'opacity-100'}`}>
              <FaBars size={24} className="text-[#4ADE80]" />
            </div>
          </button>
        </div>
      </div>

      {/* Hero Section with Split Navigation */}
      <div className="hidden md:block fixed inset-0 z-40">
        <div className="absolute inset-0 flex justify-between">
          {/* Left Navigation */}
          <div className="flex flex-col items-start justify-center pl-8 space-y-12">
            {leftNavItems.map(({ href, label }) => (
              <Link 
                key={href}
                href={href}
                className={`relative text-4xl font-medium transition-all duration-200 group drop-shadow-lg font-poppins ${
                  isActive(href) 
                    ? 'text-[#4ADE80]' 
                    : 'text-white hover:text-[#4ADE80]'
                }`}
              >
                <span className="relative z-10">{label}</span>
                <span className={`absolute inset-0 rounded-lg transition-all duration-200 ${
                  isActive(href)
                    ? 'bg-[#4ADE80]/10'
                    : 'bg-transparent group-hover:bg-[#4ADE80]/5'
                }`}></span>
              </Link>
            ))}
          </div>

          {/* Right Navigation */}
          <div className="flex flex-col items-end justify-center pr- space-y-12">
            {rightNavItems.map(({ href, label }) => (
              <div key={href} className="w-full flex justify-end">
                <Link 
                  href={href}
                  className={`relative text-4xl font-medium transition-all duration-200 group drop-shadow-lg font-poppins ${
                    isActive(href) 
                      ? 'text-[#4ADE80]' 
                      : 'text-white hover:text-[#4ADE80]'
                  }`}
                >
                  <span className="relative z-10">{label}</span>
                  <span className={`absolute inset-0 rounded-lg transition-all duration-200 ${
                    isActive(href)
                      ? 'bg-[#4ADE80]/10'
                      : 'bg-transparent group-hover:bg-[#4ADE80]/5'
                  }`}></span>
                </Link>
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* Mobile menu */}
      <div 
        className={`fixed inset-0 z-40 bg-[#0A5C35]/95 backdrop-blur-md transition-all duration-300 ease-in-out transform ${
          isMobileMenuOpen ? 'translate-x-0 opacity-100' : 'translate-x-full opacity-0 pointer-events-none'
        }`}
      >
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 pt-20">
          <div className="flex flex-col space-y-1">
            {navigationItems.map(({ href, label }) => (
              <Link 
                key={href}
                href={href}
                className={`px-4 py-3 rounded-lg text-xl font-medium transition-all duration-200 font-poppins ${
                  isActive(href)
                    ? 'text-[#4ADE80] bg-[#4ADE80]/10'
                    : 'text-[#E2E8F0] hover:text-[#4ADE80] hover:bg-[#4ADE80]/5'
                }`}
                onClick={closeMobileMenu}
              >
                {label}
              </Link>
            ))}
          </div>
        </div>
      </div>
    </nav>
  );
};

export default Navbar; 